import { Bezier } from 'bezier-js'
import { ComplexArray } from './fft'
import waveTables from './waveTables'
import pianoKit from '@jasonfleischer/piano'
import musicKit from '@jasonfleischer/music-model-kit'
import BezierMorpher from './bezierMorpher'
musicKit.init()

const canvas = document.getElementById('myCanvas')
const ctx = canvas.getContext('2d')
const audioContext = new AudioContext()
const analyser = new AnalyserNode(audioContext, {
  smoothingTimeConstant: 1,
  fftSize: 2048
})
const analyserDataArray = new Uint8Array(analyser.frequencyBinCount)
analyser.connect(audioContext.destination)

const fftAnalyser = new AnalyserNode(audioContext, {
  fftSize: 256
})
fftAnalyser.connect(audioContext.destination)
const fftAnalyserDataArray = new Uint8Array(analyser.frequencyBinCount)

const waveOffset = { x: 40, y: 40 }

const waveTablesNames = Object.keys(waveTables)
let selectedWaveTable = 'Bass'
let generatedWaveTable = {}

pianoKit({
  id: 'piano',
  range: {
    min: 36,
    max: 84 // midi value = C5
  },
  width: 600,
  onClick: function ({ frequency }, isOn) {
    let real, imag
    if (useWaveForm === 'example') {
      real = waveTables[selectedWaveTable].real
      imag = waveTables[selectedWaveTable].imag
    } else if (useWaveForm === 'generated') {
      real = generatedWaveTable.real
      imag = generatedWaveTable.imag
    } else if (useWaveForm === 'morphed') {
      const morphedWavetable = bezierMorpher.getWaveTable()
      real = morphedWavetable.real
      imag = morphedWavetable.imag
    }
    const gain = audioContext.createGain()
    gain.connect(audioContext.destination)
    gain.connect(analyser)
    gain.connect(fftAnalyser)
    gain.gain.value = 1
    const oscillator = audioContext.createOscillator()
    oscillator.connect(audioContext.destination)
    oscillator.connect(gain)
    const wave = audioContext.createPeriodicWave(real, imag)
    oscillator.setPeriodicWave(wave)
    oscillator.frequency.value = frequency
    const currentTime = audioContext.currentTime
    gain.gain.setValueAtTime(-1, currentTime)
    oscillator.start()
    gain.gain.linearRampToValueAtTime(0.8, currentTime + 0.1)
    gain.gain.linearRampToValueAtTime(0.6, currentTime + 0.3)
    gain.gain.linearRampToValueAtTime(0, currentTime + 1)
    oscillator.stop(currentTime + 1)
  },
  hover: true
})

const wave = [
  new Bezier(0, 100, 0, 100, 0, 0, 0, 0),
  new Bezier(0, 0, 0, 0, 200, 0, 200, 0),
  new Bezier(200, 0, 200, 0, 200, 100, 200, 100),
  new Bezier(200, 100, 200, 100, 200, 200, 200, 200),
  new Bezier(200, 200, 200, 200, 400, 200, 400, 200),
  new Bezier(400, 200, 400, 200, 400, 100, 400, 100)
]

const bezierMorpher = new BezierMorpher({ canvasId: 'waveMorpher', currentBezierData: wave })
bezierMorpher.updated = () => {
  drawGraphToCanvas('waveMorphResult', bezierMorpher.morphedWaveFormData)
}

let moveType = 'point'
const radioButtons = document.querySelectorAll("input[name='bezierHandler']")
for (const radioButton of radioButtons) {
  radioButton.addEventListener('click', ({ target: { value } }) => {
    moveType = value
  })
}

let useWaveForm = 'example'
const radioButtonsWaveForm = document.querySelectorAll("input[name='useWaveForm']")
for (const radioButtonWaveForm of radioButtonsWaveForm) {
  radioButtonWaveForm.addEventListener('click', ({ target: { value } }) => {
    useWaveForm = value
  })
}

const filterRange = document.getElementById('filterRange')
let filterValue = filterRange.value
filterRange.addEventListener('change', ({ target: { value } }) => {
  filterValue = value
  bezierMorpher.updateFilterValue(value)
  calculateWave(wave, filterValue)
})

const select = document.getElementById('waveformInputSelector')
for (const waveTableName of waveTablesNames) {
  const option = document.createElement('option')
  option.textContent = waveTableName
  option.value = waveTableName
  select.appendChild(option)
}
select.addEventListener('change', ({ target: { value } }) => {
  useWaveForm = 'example'
  const radiobutton = document.getElementById('example')
  radiobutton.checked = true
  selectedWaveTable = value
  drawWave(wave)
  calculateWave(wave, filterRange)
})
const shiftPhaseButton = document.getElementById('shiftPhase')
shiftPhaseButton.addEventListener('click', (event) => {
  console.log('click phase shift')
  waveTables[selectedWaveTable].waveForm = shiftWaveFrom(waveTables[selectedWaveTable].waveForm)
  drawWave(wave)
  calculateWave(wave, filterRange)
})

const initOscilloscope = () => {
  const oscilloscopeCanvas = document.getElementById('oscilloscope')
  const c = oscilloscopeCanvas.getContext('2d')
  c.fillStyle = '#181818'
  c.fillRect(0, 0, oscilloscopeCanvas.width, oscilloscopeCanvas.height)
  c.strokeStyle = '#33ee55'
  c.beginPath()
  c.moveTo(0, oscilloscopeCanvas.height / 2)
  c.lineTo(oscilloscopeCanvas.width, oscilloscopeCanvas.height / 2)
  c.stroke()

  const draw = () => {
    analyser.getByteTimeDomainData(analyserDataArray)
    const segmentWidth = oscilloscopeCanvas.width / analyser.frequencyBinCount
    c.fillStyle = '#181818'
    c.fillRect(0, 0, oscilloscopeCanvas.width, oscilloscopeCanvas.height)
    c.font = '10px Monaco'
    c.fillStyle = '#33ee55'
    c.fillText('waveform', 5, 15)
    c.beginPath()
    c.moveTo(-100, oscilloscopeCanvas.height / 2)
    for (let i = 1; i < analyser.frequencyBinCount; i += 1) {
      const x = i * segmentWidth
      const v = analyserDataArray[i] / 128.0
      const y = (v * oscilloscopeCanvas.height) / 2
      c.lineTo(x, y)
    }
    c.lineTo(oscilloscopeCanvas.width + 100, oscilloscopeCanvas.height / 2)
    c.stroke()
    requestAnimationFrame(draw)
  }
  draw()

  const fftCanvas = document.getElementById('fftScope')
  const fftContext = fftCanvas.getContext('2d')
  fftContext.fillStyle = '#181818'
  fftContext.fillRect(0, 0, fftCanvas.width, fftCanvas.height)
  fftContext.strokeStyle = '#33ee55'
  const drawFft = () => {
    fftAnalyser.getByteFrequencyData(fftAnalyserDataArray)
    const bufferLength = fftAnalyser.frequencyBinCount
    fftContext.fillStyle = '#181818'
    fftContext.fillRect(0, 0, fftCanvas.width, fftCanvas.height)
    fftContext.font = '10px Monaco'
    fftContext.fillStyle = '#33ee55'
    fftContext.fillText('frequency', 5, 15)
    let x = 0
    const barWidth = (fftCanvas.width / bufferLength)
    for (let i = 0; i < bufferLength; i++) {
      barHeight = fftAnalyserDataArray[i] / 4
      fftContext.fillRect(
        x,
        fftCanvas.height - barHeight,
        1,
        barHeight
      )

      x += barWidth
    }
    requestAnimationFrame(drawFft)
  }
  drawFft()
}

const mapToRange = (value, a, b, c, d) => {
  const scale = (d - c) / (b - a)
  const offset = -a * (d - c) / (b - a) + c
  return value * scale + offset
}

const drawCurve = (curve, offset) => {
  offset = offset || waveOffset
  const ox = offset.x
  const oy = offset.y
  ctx.beginPath()
  const p = curve.points
  let i
  ctx.moveTo(p[0].x + ox, p[0].y + oy)
  if (p.length === 3) {
    ctx.quadraticCurveTo(p[1].x + ox, p[1].y + oy, p[2].x + ox, p[2].y + oy)
  }
  if (p.length === 4) {
    ctx.bezierCurveTo(
      p[1].x + ox,
      p[1].y + oy,
      p[2].x + ox,
      p[2].y + oy,
      p[3].x + ox,
      p[3].y + oy
    )
  }
  ctx.stroke()
  ctx.closePath()
}

const drawSkeleton = (curve, offset, nocoords) => {
  offset = offset || waveOffset
  const pts = curve.points
  ctx.strokeStyle = 'lightgrey'
  drawLine(pts[0], pts[1], offset)
  if (pts.length === 3) {
    drawLine(pts[1], pts[2], offset)
  } else {
    drawLine(pts[2], pts[3], offset)
  }
  ctx.strokeStyle = 'black'
  if (!nocoords) drawPoints(pts, offset)
}

const drawLine = (p1, p2, offset) => {
  offset = offset || waveOffset
  const ox = offset.x
  const oy = offset.y
  ctx.beginPath()
  ctx.moveTo(p1.x + ox, p1.y + oy)
  ctx.lineTo(p2.x + ox, p2.y + oy)
  ctx.stroke()
}
const drawPoints = (points, offset) => {
  offset = offset || waveOffset
  points.forEach((p) => drawCircle(p, 3, offset))
}

const drawCircle = (p, r, offset) => {
  offset = offset || waveOffset
  const ox = offset.x
  const oy = offset.y
  ctx.beginPath()
  ctx.arc(p.x + ox, p.y + oy, r, 0, 2 * Math.PI)
  ctx.stroke()
}

const drawSelectedWaveTable = (data) => {
  const width = canvas.clientWidth
  const height = canvas.clientHeight
  const n = data.length
  ctx.fillStyle = 'none'
  ctx.strokeStyle = 'blue'
  ctx.beginPath()
  data.forEach((value, i) => {
    ctx.lineTo(mapToRange(i, 0, n, 0, width - 2 * waveOffset.y) + waveOffset.x, value + waveOffset.x)
  })
  ctx.stroke()
}

const drawWaveBox = () => {
  const width = canvas.clientWidth
  const height = canvas.clientHeight
  ctx.fillStyle = 'mintCream'
  ctx.fillRect(0, 0, width, height)
  ctx.fillStyle = 'ivory'
  ctx.fillRect(0 + waveOffset.x, 0 + waveOffset.y, width - 2 * waveOffset.x, height - 2 * waveOffset.y)
  ctx.strokeStyle = 'lightBlue'
  ctx.beginPath()
  ctx.rect(0 + waveOffset.x, 0 + waveOffset.y, width - 2 * waveOffset.x, height - 2 * waveOffset.y)
  ctx.stroke()
  ctx.beginPath()
  ctx.moveTo(0, height / 2)
  ctx.lineTo(width, height / 2)
  ctx.stroke()
}

const reset = (curve, evt) => {
  ctx.strokeStyle = 'black'
  ctx.fillStyle = 'none'
  if (evt && curve) {
    curve.mouse = { x: evt.offsetX, y: evt.offsetY }
  }
}

const drawWave = (wave) => {
  drawWaveBox()
  if (selectedWaveTable && Array.isArray(waveTables[selectedWaveTable].waveForm)) {
    drawSelectedWaveTable(waveTables[selectedWaveTable].waveForm)
  }
  for (const curve of wave) {
    drawSkeleton(curve)
    drawCurve(curve)
  }
}

const resetWave = (wave, evt) => {
  for (const curve of wave) {
    reset(curve, evt)
  }
}

const addInteraction = (wave, offset) => {
  const lpts = []
  const pointType = []

  wave.forEach((curve, curveIndex) => {
    lpts.push(...curve.points)
    for (let index = 0; index < 4; index++) {
      switch (index) {
        case 0:
          pointType.push({
            type: 'point',
            position: 'first'
          })
          break
        case 1:
          pointType.push({
            type: 'handler',
            position: 'first'
          })
          break
        case 3:
          pointType.push({
            type: 'point',
            position: 'last'
          })
          break
        case 2:
          pointType.push({
            type: 'handler',
            position: 'last'
          })
          break
      }
    }
  })

  let moving = false
  let mx = 0
  let my = 0
  let ox = 0
  let oy = 0
  let cx
  let cy
  let mp = false
  let linkedPoint = false
  let leftHandler
  let leftHandlerX
  let leftHandlerY
  let leftLeftHandler
  let leftLeftHandlerX
  let leftLeftHandlerY
  let rightHandler
  let rightHandlerX
  let rightHandlerY
  let rightRightHandler
  let rightRightHandlerX
  let rightRightHandlerY
  let xConstrains
  let type

  canvas.addEventListener('mousedown', function (evt) {
    mx = evt.offsetX
    my = evt.offsetY

    let found = false

    for (let i = 1; i < lpts.length - 1; i++) {
      const p = lpts[i]
      const pType = pointType[i]
      if (moveType === 'handle' && pType.type === 'handler' && Math.abs(mx - p.x - waveOffset.x) < 10 && Math.abs(my - p.y - waveOffset.y) < 10) {
        console.log(p, pType, i)
        moving = true
        mp = p
        cx = p.x
        cy = p.y
        type = pType
        xConstrains = {
          minX: pType.position === 'first' ? lpts[i - 1].x : lpts[i - 2].x,
          maxX: pType.position === 'first' ? lpts[i + 2].x : lpts[i + 1].x
        }
        found = true
        break
      }
    }

    if (!found) {
      for (let i = 1; i < lpts.length - 1; i++) {
        const p = lpts[i]
        const pType = pointType[i]
        if (moveType === 'point' && pType.type === 'point' && pType.position === 'first' && Math.abs(mx - p.x - waveOffset.x) < 10 && Math.abs(my - p.y - waveOffset.y) < 10) {
          moving = true
          mp = p
          cx = p.x
          cy = p.y
          type = pType
          linkedPoint = lpts[i - 1]
          leftHandler = lpts[i - 2]
          leftHandlerX = leftHandler.x
          leftHandlerY = leftHandler.y
          leftLeftHandler = lpts[i - 3]
          leftLeftHandlerX = leftLeftHandler.x
          leftLeftHandlerY = leftLeftHandler.y
          rightHandler = lpts[i + 1]
          rightHandlerX = rightHandler.x
          rightHandlerY = rightHandler.y
          rightRightHandler = lpts[i + 2]
          rightRightHandlerX = rightRightHandler.x
          rightRightHandlerY = rightRightHandler.y
          xConstrains = {
            minX: lpts[i - 4].x,
            maxX: lpts[i + 3].x
          }
        }
      }
    }
  })

  canvas.addEventListener('mousemove', function (evt) {
    let found = false

    if (!lpts) return
    lpts.forEach(function (p) {
      const mx = evt.offsetX
      const my = evt.offsetY
      if (Math.abs(mx - p.x - waveOffset.x) < 10 && Math.abs(my - p.y - waveOffset.y) < 10) {
        found = found || true
      }
    })
    canvas.style.cursor = found ? 'pointer' : 'default'

    if (!moving) {
      resetWave(wave, evt)
      drawWave(wave)
      return
    }

    ox = evt.offsetX - mx
    oy = evt.offsetY - my
    mp.x = cx + ox
    mp.y = cy + oy

    // HandlerConstrain should be a function looping over all the data points.
    if (mp.x < xConstrains.minX) {
      mp.x = xConstrains.minX
    }
    if (mp.x > xConstrains.maxX) {
      mp.x = xConstrains.maxX
    }

    if (type && type.type === 'point' && linkedPoint) {
      if (mp.y > 200) {
        mp.y = 200
      }
      if (mp.y < 0) {
        mp.y = 0
      }
      linkedPoint.x = mp.x
      linkedPoint.y = mp.y

      leftHandler.x = leftHandlerX + ox
      leftHandler.y = leftHandlerY + oy
      if (leftHandler.x < xConstrains.minX) {
        leftHandler.x = xConstrains.minX
      }
      if (leftHandler.x > xConstrains.maxX) {
        leftHandler.x = xConstrains.maxX
      }
      rightHandler.x = rightHandlerX + ox
      rightHandler.y = rightHandlerY + oy
      if (rightHandler.x < xConstrains.minX) {
        rightHandler.x = xConstrains.minX
      }
      if (rightHandler.x > xConstrains.maxX) {
        rightHandler.x = xConstrains.maxX
      }
      if (leftLeftHandler.x < mp.x) {
        leftLeftHandler.x = leftLeftHandlerX
      } else {
        leftLeftHandler.x = mp.x
      }
      if (rightRightHandler.x > mp.x) {
        rightRightHandler.x = rightRightHandlerX
      } else {
        rightRightHandler.x = mp.x
      }
    }

    for (const curve of wave) {
      curve.update()
    }
    resetWave(wave, evt)
    drawWave(wave)
    calculateWave(wave, filterValue)
  })

  canvas.addEventListener('mouseup', function (evt) {
    if (!moving) return
    // console.log(curve.points.map(function(p) { return p.x+", "+p.y; }).join(", "));
    moving = false
    mp = false
    linkedPoint = false
  })

  canvas.addEventListener('dblclick', (event) => {
    const mouseX = event.offsetX
    const mouseY = event.offsetY
    console.log('dobuleClick')
    for (let i = 1; i < lpts.length - 1; i++) {
      const p = lpts[i]
      const pType = pointType[i]
      console.log('dobuleClick I')
      console.log(pType, p, mouseX, mouseY)
      if (pType.type === 'point' && pType.position === 'first' && Math.abs(mouseX - p.x - waveOffset.x) < 10 && Math.abs(mouseY - p.y - waveOffset.y) < 10) {
        console.log('dobuleClick', p, pType, i)
        const leftHandler = lpts[i - 2]
        const rightHandler = lpts[i + 1]
        leftHandler.x = p.x
        leftHandler.y = p.y
        rightHandler.x = p.x
        rightHandler.y = p.y
        resetWave(wave, event)
        drawWave(wave)
      }
    }
  })
}

const drawGraphToCanvas = (elementId, data, showSelectedWaveTable) => {
  const drawCanvas = document.getElementById(elementId)
  const width = drawCanvas.clientWidth
  const height = drawCanvas.clientHeight
  const offset = {
    x: 10, y: 10
  }
  const n = data.length
  const context = drawCanvas.getContext('2d')
  context.reset()
  context.fillStyle = 'white'
  context.fillRect(0, 0, width, height)
  context.strokeStyle = 'lightGrey'
  context.beginPath()
  context.moveTo(0, offset.x)
  context.lineTo(width, offset.x)
  context.stroke()
  context.beginPath()
  context.moveTo(0, height / 2)
  context.lineTo(width, height / 2)
  context.stroke()
  context.beginPath()
  context.moveTo(0, height - offset.x)
  context.lineTo(width, height - offset.x)
  context.stroke()
  if (showSelectedWaveTable && selectedWaveTable && Array.isArray(waveTables[selectedWaveTable].waveForm)) {
    context.strokeStyle = 'red'
    context.beginPath()
    const waveformlength = waveTables[selectedWaveTable].waveForm.length
    waveTables[selectedWaveTable].waveForm.forEach((value, i) => {
      context.lineTo(mapToRange(i, 0, waveformlength, 0, width - offset.x * 2) + offset.x, mapToRange(value, 0, 200, offset.y, height - offset.y))
    })
    context.stroke()
  }
  context.strokeStyle = 'blue'
  context.beginPath()
  data.forEach((value, i) => {
    context.lineTo(mapToRange(i, 0, n, 0, width - offset.x * 2) + offset.x, mapToRange(value, 0, 200, offset.y, height - offset.y))
  })
  context.stroke()
}

const calculateWave = (wave, filterValue) => {
  const waveForm = []
  const data = new ComplexArray(512).map((value, i, n) => {
    const x = mapToRange(i, 0, 512, 0, 400)
    const bezier = wave.find(curve => x >= curve.bbox().x.min && x <= curve.bbox().x.max && curve.bbox().x.min !== curve.bbox().x.max)
    const line = { p1: { x, y: 0 }, p2: { x, y: 200 } }
    const t = bezier.intersects(line)[0] || 0
    const y = bezier.get(t).y
    value.real = mapToRange(y, 0, 200, 100, -100)
    waveForm.push(y)
  })
  drawGraphToCanvas('waveForm', waveForm)
  data.FFT()
  const fftValues = []
  data.map(({ real }, i) => {
    fftValues.push(mapToRange(real, -5, 3000, 0, 1000) + 100)
  })
  drawGraphToCanvas('fft', fftValues.slice(0, fftValues.length / 2))
  const fftFilteredValues = []
  data.map((freq, i, n) => {
    if (i > n / filterValue && i < (filterValue - 1) * n / filterValue) {
      freq.real = 0
      freq.imag = 0
    }
  })
  data.map(({ real }, i) => {
    fftFilteredValues.push(mapToRange(real, -5, 3000, 0, 1000) + 100)
  })
  generatedWaveTable = { real: [...data.real], imag: [...data.imag] }
  const newWaveform = []
  data.InvFFT()
  data.map(({ real }, i) => {
    newWaveform.push(mapToRange(real, 100, -100, 0, 200))
  })
  drawGraphToCanvas('bandLimitedWaveForm', newWaveform, true)
  bezierMorpher.updateCurrentBezierData(wave)
}

const shiftWaveFrom = (waveForm) => {
  for (let index = 0; index < waveForm.length - 1; index++) {
    const value = waveForm[index]
    const nextValue = waveForm[index + 1]
    if (value > 100 && nextValue <= 100) {
      waveForm = [...waveForm.slice(index + 1), ...waveForm.slice(0, index + 1)]
      break
    }
  }
  return waveForm
}

for (const name of waveTablesNames) {
  const waveTable = waveTables[name]
  if (waveTable.real) {
    const data = new ComplexArray(waveTable.real.length).map((value, i, n) => {
      value.real = waveTable.real[i]
      value.imag = waveTable.imag[i]
    })
    data.InvFFT()
    waveTables[name].waveForm = [...data.real].map(v => mapToRange(v, -0.03, 0.03, 200, 0))
  } else {
    const data = new ComplexArray(waveTable.waveForm.length).map((value, i, n) => {
      value.real = waveTable.waveForm[i]
    })
    const waveFormMax = Math.max(...waveTable.waveForm)
    const waveFormMin = Math.min(...waveTable.waveForm)
    waveTable.waveForm = waveTable.waveForm.map(value => mapToRange(value, waveFormMin, waveFormMax, 0, 200))
    data.FFT()
    waveTable.real = data.real
    waveTable.imag = data.imag
  }

  waveTables[name].waveForm = shiftWaveFrom(waveTables[name].waveForm)
}

drawWave(wave)
addInteraction(wave)
calculateWave(wave, filterValue)
initOscilloscope()
