import { Bezier } from 'bezier-js'
import { ComplexArray } from './fft'

const mapToRange = (value, a, b, c, d) => {
  const scale = (d - c) / (b - a)
  const offset = -a * (d - c) / (b - a) + c
  return value * scale + offset
}

const defaultBezierWaveForms = [
  [
    {
      curves: [
        new Bezier(0, 100, 0, 100, 0, 0, 0, 0),
        new Bezier(0, 0, 0, 0, 200, 0, 200, 0),
        new Bezier(200, 0, 200, 0, 200, 100, 200, 100),
        new Bezier(200, 100, 200, 100, 200, 200, 200, 200),
        new Bezier(200, 200, 200, 200, 400, 200, 400, 200),
        new Bezier(400, 200, 400, 200, 400, 100, 400, 100)
      ],
      offset: {},
      bbox: []
    }, {
      curves: [
        new Bezier(0, 100, 0, 100, 0, 0, 0, 0),
        new Bezier(0, 0, 0, 0, 100, 50, 100, 50),
        new Bezier(100, 50, 100, 50, 200, 100, 200, 100),
        new Bezier(200, 100, 200, 100, 300, 150, 300, 150),
        new Bezier(300, 150, 300, 150, 400, 200, 400, 200),
        new Bezier(400, 200, 400, 200, 400, 100, 400, 100)
      ],
      offset: {},
      bbox: []
    }
  ], [
    {
      curves: [
        new Bezier(0, 100, 0, 100, 0, 0, 0, 0),
        new Bezier(0, 0, 0, 0, 200, 0, 200, 0),
        new Bezier(200, 0, 200, 0, 200, 100, 200, 100),
        new Bezier(200, 100, 200, 100, 200, 200, 200, 200),
        new Bezier(200, 200, 200, 200, 400, 200, 400, 200),
        new Bezier(400, 200, 400, 200, 400, 100, 400, 100)
      ],
      offset: {},
      bbox: []
    }, {
      curves: [
        new Bezier(0, 100, 0, 100, 0, 0, 0, 0),
        new Bezier(0, 0, 0, 0, 100, 50, 100, 50),
        new Bezier(100, 50, 100, 50, 200, 100, 200, 100),
        new Bezier(200, 100, 200, 100, 300, 150, 300, 150),
        new Bezier(300, 150, 300, 150, 400, 200, 400, 200),
        new Bezier(400, 200, 400, 200, 400, 100, 400, 100)
      ],
      offset: {},
      bbox: []
    }
  ]
]

class BezierMorpher {
  constructor ({
    canvasId,
    currentBezierData
  }) {
    this.canvasId = canvasId
    this.currentBezierData = currentBezierData
    this.bezierWaveforms = defaultBezierWaveForms
    this.morphedShapesWaveforms = [{}, {}]
    this.morphedVersionWavefrom = {}
    this.morphedWaveForm = {}
    this.morphedWaveFormData = []
    this.shapeSlider = 0.3
    this.versionSlider = 0.5
    this.filterValue = 5
    this.sliders = [
      {
        position: this.shapeSlider,
        parameter: 'shapeSlider',
        from: { x: 30, y: 100 },
        to: { x: 30, y: 180 },
        sliderLocation: {},
        moving: false
      },
      {
        position: this.versionSlider,
        parameter: 'versionSlider',
        from: { x: 180, y: 200 },
        to: { x: 300, y: 200 },
        sliderLocation: {},
        moving: false
      }
    ]
    this.updated = () => {}

    this.canvas = document.getElementById(canvasId)
    this.context = this.canvas.getContext('2d')

    this.calculate()
    this.calculateOffsetBbox()
    this.calculateWaveTable()
    this.drawSliders()
    this.render()
    this.addEvents()
    this.updated()
  }

  updateCurrentBezierData (currentBezierData) {
    this.currentBezierData = currentBezierData
  }

  updateFilterValue (filterValue) {
    this.filterValue = filterValue
    this.calculate()
    this.calculateWaveTable()
    this.updated()
  }

  addEvents () {
    const that = this
    this.canvas.addEventListener('mousemove', function (event) {
      const mouseX = event.offsetX
      const mouseY = event.offsetY
      let found = false
      for (const shapeWaveform of that.bezierWaveforms) {
        for (const versionWaveform of shapeWaveform) {
          const bbox = versionWaveform.bbox
          if (mouseX > bbox[0].x && mouseX < bbox[1].x && mouseY > bbox[0].y && mouseY < bbox[1].y) {
            found = found || true
          }
        }
      }
      for (const slider of that.sliders) {
        const { x, y } = slider.sliderLocation

        if (Math.abs(mouseX - x) < 10 && Math.abs(mouseY - y) < 10) {
          found = found || true
        }
      }
      that.canvas.style.cursor = found ? 'pointer' : 'default'

      const movingSlider = that.sliders.find(s => s.moving === true)
      if (movingSlider) {
        movingSlider.sliderLocation = { x: mouseX, y: mouseY }
        if (movingSlider.sliderLocation.x < movingSlider.from.x) {
          movingSlider.sliderLocation.x = movingSlider.from.x
        }
        if (movingSlider.sliderLocation.y < movingSlider.from.y) {
          movingSlider.sliderLocation.y = movingSlider.from.y
        }
        if (movingSlider.sliderLocation.x > movingSlider.to.x) {
          movingSlider.sliderLocation.x = movingSlider.to.x
        }
        if (movingSlider.sliderLocation.y > movingSlider.to.y) {
          movingSlider.sliderLocation.y = movingSlider.to.y
        }
        let percentage
        if (movingSlider.to.x === movingSlider.from.x) {
          percentage = (movingSlider.sliderLocation.y - movingSlider.from.y) / (movingSlider.to.y - movingSlider.from.y)
        } else if (movingSlider.to.y === movingSlider.from.y) {
          percentage = (movingSlider.sliderLocation.x - movingSlider.from.x) / (movingSlider.to.x - movingSlider.from.x)
        }
        movingSlider.position = percentage
        that[movingSlider.parameter] = percentage
        that.calculate()
        that.calculateWaveTable()
      }

      that.render()
      that.updated()
    })
    this.canvas.addEventListener('mousedown', function (event) {
      const mouseX = event.offsetX
      const mouseY = event.offsetY
      let found = false
      for (const slider of that.sliders) {
        const { x, y } = slider.sliderLocation
        if (Math.abs(mouseX - x) < 10 && Math.abs(mouseY - y) < 10) {
          found = found || true
          slider.moving = true
        }
      }
    })

    this.canvas.addEventListener('mouseup', function (event) {
      for (const slider of that.sliders) {
        if (slider.moving) {
          slider.moving = false
        }
      }
    })

    this.canvas.addEventListener('dblclick', function (event) {
      const mouseX = event.offsetX
      const mouseY = event.offsetY
      for (const shapeWaveform of that.bezierWaveforms) {
        for (const versionWaveform of shapeWaveform) {
          const bbox = versionWaveform.bbox
          if (mouseX > bbox[0].x && mouseX < bbox[1].x && mouseY > bbox[0].y && mouseY < bbox[1].y) {
            console.log(versionWaveform, bbox, mouseX, mouseY)
            const newCurve = []
            for (const bezier of that.currentBezierData) {
              const points = []
              bezier.points.forEach(point => {
                points.push(point.x)
                points.push(point.y)
              })
              newCurve.push(new Bezier(...points))
            }
            versionWaveform.curves = newCurve
            that.calculate()
            that.calculateWaveTable()
            that.render()
          }
        }
      }
    })
  }

  getWaveTable () {
    return this.morphedWaveForm
  }

  calculate () {
    let shapeIndex = 0
    for (const shapeBezierWaveforms of this.bezierWaveforms) {
      let newCurves = []
      if (this.shapeSlider === 0) {
        newCurves = shapeBezierWaveforms[0].curves
      } else if (this.shapeSlider === 1) {
        newCurves = shapeBezierWaveforms[1].curves
      } else {
        for (let index = 0; index < shapeBezierWaveforms[0].curves.length; index++) {
          const firstCurve = shapeBezierWaveforms[0].curves[index].points
          const secondCurve = shapeBezierWaveforms[1].curves[index].points
          const newPoints = []
          for (let pointIndex = 0; pointIndex < firstCurve.length; pointIndex++) {
            const pointA = firstCurve[pointIndex]
            const pointB = secondCurve[pointIndex]
            const newPointx = pointA.x + (pointB.x - pointA.x) * this.shapeSlider
            const newPointy = pointA.y + (pointB.y - pointA.y) * this.shapeSlider
            newPoints.push(newPointx)
            newPoints.push(newPointy)
          }
          newCurves.push(new Bezier(...newPoints))
        }
      }
      this.morphedShapesWaveforms[shapeIndex].curves = newCurves
      shapeIndex++
    }

    let newCurves = []
    if (this.versionSlider === 0) {
      newCurves = this.morphedShapesWaveforms[0].curves
    } else if (this.versionSlider === 1) {
      newCurves = this.morphedShapesWaveforms[1].curves
    } else {
      for (let index = 0; index < this.morphedShapesWaveforms[0].curves.length; index++) {
        const firstCurve = this.morphedShapesWaveforms[0].curves[index].points
        const secondCurve = this.morphedShapesWaveforms[1].curves[index].points
        const newPoints = []
        for (let pointIndex = 0; pointIndex < firstCurve.length; pointIndex++) {
          const pointA = firstCurve[pointIndex]
          const pointB = secondCurve[pointIndex]
          const newPointx = pointA.x + (pointB.x - pointA.x) * this.versionSlider
          const newPointy = pointA.y + (pointB.y - pointA.y) * this.versionSlider
          newPoints.push(newPointx)
          newPoints.push(newPointy)
        }
        newCurves.push(new Bezier(...newPoints))
      }
    }
    this.morphedVersionWavefrom.curves = newCurves
  }

  calculateWaveTable () {
    const wave = this.morphedVersionWavefrom.curves
    const waveForm = []
    const data = new ComplexArray(512).map((value, i, n) => {
      const x = mapToRange(i, 0, 512, 0, 400)
      const bezier = wave.find(curve => x >= curve.bbox().x.min && x <= curve.bbox().x.max)
      const line = { p1: { x, y: 0 }, p2: { x, y: 200 } }
      const t = bezier.intersects(line)[0] || 0
      const y = bezier.get(t).y
      value.real = mapToRange(y, 0, 200, 100, -100)
      waveForm.push(y)
    })
    data.FFT()
    const fftValues = []
    data.map(({ real }, i) => {
      fftValues.push(mapToRange(real, -5, 3000, 0, 1000) + 100)
    })
    const fftFilteredValues = []
    data.map((freq, i, n) => {
      if (i > n / this.filterValue && i < (this.filterValue - 1) * n / this.filterValue) {
        freq.real = 0
        freq.imag = 0
      }
    })
    data.map(({ real }, i) => {
      fftFilteredValues.push(mapToRange(real, -5, 3000, 0, 1000) + 100)
    })
    this.morphedWaveForm = { real: [...data.real], imag: [...data.imag] }
    const newWaveform = []
    data.InvFFT()
    data.map(({ real }, i) => {
      newWaveform.push(mapToRange(real, 100, -100, 0, 200))
    })
    this.morphedWaveFormData = newWaveform
  }

  calculateOffsetBbox () {
    const canvas = this.canvas
    const width = canvas.width
    const height = canvas.height
    this.bezierWaveforms[0][0] = {
      curves: this.bezierWaveforms[0][0].curves,
      offset: { x: 20, y: 20 },
      scale: 4,
      bbox: [{ x: 20, y: 20 }, { x: 20 + 100, y: 20 + 50 }]
    }
    this.bezierWaveforms[0][1] = {
      curves: this.bezierWaveforms[0][1].curves,
      offset: { x: 20, y: height - 20 - 50 },
      scale: 4,
      bbox: [{ x: 20, y: height - 20 - 50 }, { x: 20 + 100, y: height - 20 - 50 + 50 }]
    }
    this.bezierWaveforms[1][0] = {
      curves: this.bezierWaveforms[1][0].curves,
      offset: { x: width - 20 - 100, y: 20 },
      scale: 4,
      bbox: [{ x: width - 20 - 100, y: 20 }, { x: width - 20 - 100 + 100, y: 20 + 50 }]
    }
    this.bezierWaveforms[1][1] = {
      curves: this.bezierWaveforms[1][1].curves,
      offset: { x: width - 20 - 100, y: height - 20 - 50 },
      scale: 4,
      bbox: [{ x: width - 20 - 100, y: height - 20 - 50 }, { x: width - 20 - 100 + 100, y: height - 20 - 50 + 50 }]
    }
    this.morphedShapesWaveforms[0] = {
      curves: this.morphedShapesWaveforms[0].curves,
      offset: { x: 60, y: (height / 2) - 25 },
      scale: 4,
      bbox: [{ x: 60, y: (height / 2) - 25 }, { x: 60 + 100, y: (height / 2) - 25 + 50 }]
    }
    this.morphedShapesWaveforms[1] = {
      curves: this.morphedShapesWaveforms[1].curves,
      offset: { x: width - 20 - 100 - 40, y: (height / 2) - 25 },
      scale: 4,
      bbox: [{ x: width - 20 - 100 - 40, y: (height / 2) - 25 }, { x: width - 20 - 100 - 40 + 100, y: (height / 2) - 25 + 50 }]
    }
    this.morphedVersionWavefrom = {
      curves: this.morphedVersionWavefrom.curves,
      offset: { x: width / 2 - 50, y: (height / 2) - 25 },
      scale: 4,
      bbox: [{ x: width / 2 - 50, y: (height / 2) - 25 }, { x: width / 2 - 50 + 100, y: (height / 2) - 25 + 50 }]
    }
  }

  drawCurves ({ curves, offset = {}, scale = 1 }) {
    const ox = offset.x || 0
    const oy = offset.y || 0
    for (const curve of curves) {
      this.context.beginPath()
      const p = curve.points
      this.context.moveTo(p[0].x / scale + ox, p[0].y / scale + oy)
      if (p.length === 3) {
        this.context.quadraticCurveTo(p[1].x / scale + ox, p[1].y / scale + oy, p[2].x / scale + ox, p[2].y / scale + oy)
      }
      if (p.length === 4) {
        this.context.bezierCurveTo(
          p[1].x / scale + ox,
          p[1].y / scale + oy,
          p[2].x / scale + ox,
          p[2].y / scale + oy,
          p[3].x / scale + ox,
          p[3].y / scale + oy
        )
      }
      this.context.stroke()
      this.context.closePath()
    }
  }

  drawSliders () {
    for (const slider of this.sliders) {
      const from = slider.from
      const to = slider.to
      const x = from.x + (to.x - from.x) * slider.position
      const y = from.y + (to.y - from.y) * slider.position
      slider.sliderLocation = { x, y }
      this.context.beginPath()
      this.context.moveTo(from.x, from.y)
      this.context.lineTo(to.x, to.y)
      this.context.stroke()
      this.context.beginPath()
      this.context.arc(x, y, 5, 0, 2 * Math.PI)
      this.context.stroke()
    }
  }

  render () {
    const context = this.context
    context.reset()
    context.strokeStyle = 'black'
    context.fillStyle = 'none'

    this.drawCurves(this.bezierWaveforms[0][0])
    this.drawCurves(this.bezierWaveforms[0][1])
    this.drawCurves(this.bezierWaveforms[1][0])
    this.drawCurves(this.bezierWaveforms[1][1])
    this.drawCurves(this.morphedShapesWaveforms[0])
    this.drawCurves(this.morphedShapesWaveforms[1])
    this.drawCurves(this.morphedVersionWavefrom)
    this.drawSliders()
  }
}

export default BezierMorpher
